orm: uppercase;\n  vertical-align: top;\n  font-weight: bold;\n}\n\ntr {\n  border: 1px solid transparent;\n}\n\ntd {\n  border: 1px solid transparent;\n  padding: 10px;\n}\n\n/**\n * Responsive Table\n */\n\n.c-table--responsive {\n  border: 1px solid #5f5f5f;\n  border-collapse: collapse;\n  padding: 0;\n  width: 100%;\n}\n\n.c-table--responsive tr {\n  border: 1px solid #5f5f5f;\n  background-color: #adadad;\n}\n\n.c-table--responsive th,\n.c-table--responsive td {\n  padding: 10px;\n}\n\n.c-table--responsive th {\n  font-size: var(--font-size-xs, 14px);\n  text-transform: uppercase;\n  border-bottom: 1px solid #5f5f5f;\n}\n\n@media (max-width: 700px) {\n  .c-table--responsive {\n    border: 0;\n  }\n\n  .c-table--responsive thead {\n    border: none;\n    clip: rect(0 0 0 0);\n    height: 1px;\n    margin: -1px;\n    overflow: hidden;\n    padding: 0;\n    position: absolute;\n    width: 1px;\n  }\n\n  .c-table--responsive tr {\n    border-bottom: 3px solid #5f5f5f;\n    display: block;\n    margin-bottom: 10px;\n  }\n\n  .c-table--responsive tr.this-is-active td:not(:first-child) {\n    display: -webkit-box;\n    display: -ms-flexbox;\n    display: flex;\n  }\n\n  .c-table--responsive tr.this-is-active td:first-child::before {\n    content: \"- \" attr(data-label);\n  }\n\n  .c-table--responsive td {\n    border-bottom: 1px solid #5f5f5f;\n    display: -webkit-box;\n    display: -ms-flexbox;\n    display: flex;\n    -webkit-box-align: center;\n        -ms-flex-align: center;\n            align-items: center;\n    -webkit-box-pack: justify;\n        -ms-flex-pack: justify;\n            justify-content: space-between;\n    min-height: 40px;\n  }\n\n  .c-table--responsive td:first-child {\n    cursor: pointer;\n  }\n\n  .c-table--responsive td:first-child::before {\n    content: \"+ \" attr(data-label);\n  }\n\n  .c-table--responsive td:last-child {\n    border-bottom: 0;\n  }\n\n  .c-table--responsive td:not(:first-child) {\n    display: none;\n  }\n\n  .c-table--responsive td::before {\n    content: attr(data-label);\n    font-weight: bold;\n    text-transform: uppercase;\n    font-size: var(--font-size-xs, 14px);\n  }\n}\n\n/* ------------------------------------ *\\\n    $TEXT ELEMENTS\n\\* ------------------------------------ */\n\n/**\n * Text-Related Elements\n */\n\np {\n  line-height: 1.5;\n  font-family: \"Roboto\", sans-serif;\n  font-size: var(--body-font-size, 16px);\n}\n\n@media print {\n  p {\n    font-size: 12px;\n    line-height: 1.3;\n  }\n}\n\nsmall {\n  font-size: 90%;\n}\n\n/**\n * Bold\n */\n\nstrong,\nb {\n  font-weight: bold;\n}\n\n/**\n * Blockquote\n */\n\nblockquote {\n  display: -webkit-box;\n  display: -ms-flexbox;\n  display: flex;\n  -ms-flex-wrap: wrap;\n      flex-wrap: wrap;\n}\n\nblockquote::before {\n  content: \"\\201C\";\n  font-family: \"Roboto\", sans-serif;\n  font-size: 40px;\n  line-height: 1;\n  color: #fbc617;\n  min-width: 40px;\n  border-right: 6px solid #000;\n  display: block;\n  margin-right: 20px;\n}\n\nblockquote p {\n  line-height: 1.7;\n  -webkit-box-flex: 1;\n      -ms-flex: 1;\n          flex: 1;\n}\n\n/**\n * Horizontal Rule\n */\n\nhr {\n  height: 1px;\n  border: none;\n  background-color: rgba(173, 173, 173, 0.5);\n  margin: 0 auto;\n}\n\n/**\n * Abbreviation\n */\n\nabbr {\n  border-bottom: 1px dotted #5f5f5f;\n  cursor: help;\n}\n\n/* ------------------------------------ *\\\n    $LAYOUT\n\\* ------------------------------------ */\n\n/* ------------------------------------ *\\\n    $WRAPPERS & CONTAINERS\n\\* ------------------------------------ */\n\n/**\n * Wrapping element to keep content contained and centered.\n */\n\n.l-wrap {\n  margin: 0 auto;\n  padding-left: 20px;\n  padding-right: 20px;\n  width: 100%;\n  position: relative;\n}\n\n@media (min-width: 1201px) {\n  .l-wrap {\n    padding-left: 40px;\n    padding-right: 40px;\n  }\n}\n\n/**\n * Layout containers - keep content centered and within a maximum width. Also\n * adjusts left and right padding as the viewport widens.\n */\n\n.l-container {\n  max-width: 1200px;\n  margin-left: auto;\n  margin-right: auto;\n  position: relative;\n}\n\n.l-container--xl {\n  max-width: 1600px;\n}\n\n/* ------------------------------------ *\\\n    $TEXT\n\\* ------------------------------------ */\n\n/* ------------------------------------ *\\\n    $TEXT TYPES\n\\* ------------------------------------ */\n\n/**\n * Font Families\n */\n\n.u-font {\n  font-family: \"Roboto\", sans-serif;\n}\n\n.u-font--primary,\n.u-font--primary p {\n  font-family: \"Oswald\", sans-serif;\n}\n\n.u-font--secondary,\n.u-font--secondary p {\n  font-family: \"Roboto\", arial, sans-serif;\n}\n\n/**\n * Text Sizes\n */\n\n.u-font--xs {\n  font-size: var(--font-size-xs, 14px);\n}\n\n.u-font--s {\n  font-size: var(--font-size-s, 18px);\n}\n\n.u-font--m {\n  font-size: var(--font-size-m, 22px);\n}\n\n.u-font--l {\n  font-size: var(--font-size-l, 24px);\n}\n\n.u-font--xl {\n  font-size: var(--font-size-xl, 32px);\n}\n\n.u-font--xxl {\n  font-size: var(--font-size-xxl, 40px);\n}\n\n/**\n * Text Transforms\n */\n\n.u-text-transform--upper {\n  text-transform: uppercase;\n}\n\n.u-text-transform--lower {\n  text-transform: lowercase;\n}\n\n/**\n * Text Styles\n */\n\n.u-text-style--italic {\n  font-style: italic;\n}\n\n.u-font-weight--normal {\n  font-weight: normal;\n}\n\n/**\n * Text Positioning\n */\n\n.u-align--center {\n  text-align: center;\n}\n\n/**\n * Text Decorations\n */\n\n.u-text-decoration--underline {\n  text-decoration: underline;\n}\n\n/**\n * Rich text editor text\n */\n\n.o-rte-text {\n  width: 100%;\n  margin: 0 auto;\n}\n\n.o-rte-text > * + * {\n  margin-top: 20px;\n}\n\n.o-rte-text > dl dd,\n.o-rte-text > dl dt,\n.o-rte-text > ol li,\n.o-rte-text > ul li,\n.o-rte-text > p {\n  line-height: 1.5;\n  font-family: \"Roboto\", sans-serif;\n  font-size: var(--body-font-size, 16px);\n}\n\n@media print {\n  .o-rte-text > dl dd,\n  .o-rte-text > dl dt,\n  .o-rte-text > ol li,\n  .o-rte-text > ul li,\n  .o-rte-text > p {\n    font-size: 12px;\n    line-height: 1.3;\n  }\n}\n\n.o-rte-text > h1,\n.o-rte-text > h2,\n.o-rte-text > h3,\n.o-rte-text > h4,\n.o-rte-text > h5,\n.o-rte-text > h6 {\n  padding-top: 20px;\n  margin-bottom: -10px;\n}\n\n.o-rte-text h2:empty,\n.o-rte-text h3:empty,\n.o-rte-text p:empty {\n  display: none;\n}\n\n.o-rte-text > h2 + h3 {\n  margin-top: 0;\n  padding-top: 10px;\n}\n\n.o-rte-text a {\n  text-decoration: underline;\n}\n\n.o-rte-text hr {\n  margin-top: 40px;\n  margin-bottom: 40px;\n}\n\n.o-rte-text code,\n.o-rte-text pre {\n  font-size: 125%;\n}\n\n.o-rte-text ol,\n.o-rte-text ul {\n  padding-left: 0;\n  margin-left: 0;\n}\n\n.o-rte-text ol li,\n.o-rte-text ul li {\n  list-style: none;\n  padding-left: 34px;\n  margin-left: 0;\n  position: relative;\n  line-height: 2.1em;\n}\n\n.o-rte-text ol li::before,\n.o-rte-text ul li::before {\n  color: #fbc617;\n  width: 10px;\n  display: inline-block;\n  position: absolute;\n  left: 0;\n  font-size: 24px;\n  line-height: 1;\n  top: 4px;\n}\n\n.o-rte-text ol li li,\n.o-rte-text ul li li {\n  list-style: none;\n}\n\n.o-rte-text ol {\n  counter-reset: item;\n}\n\n.o-rte-text ol li::before {\n  content: counter(item) \". \";\n  counter-increment: item;\n}\n\n.o-rte-text ol li li {\n  counter-reset: item;\n}\n\n.o-rte-text ol li li::before {\n  content: '\\2010';\n}\n\n.o-rte-text ul li::before {\n  content: '\\2022';\n}\n\n.o-rte-text ul li li::before {\n  content: '\\25E6';\n}\n\n/* ------------------------------------ *\\\n    $BUTTONS\n\\* ------------------------------------ */\n\n/**\n * Button Primary\n */\n\n.o-button--primary {\n  display: -webkit-inline-box;\n  display: -ms-inline-flexbox;\n  display: inline-flex;\n  position: relative;\n  -webkit-box-pack: center;\n      -ms-flex-pack: center;\n          justify-content: center;\n  -webkit-box-align: center;\n      -ms-flex-align: center;\n          align-items: center;\n  -webkit-transition: all 0.23s cubic-bezier(0.86, 0, 0.07, 1)