arge\") {\n      &::before {\n        content: \"xxxlarge: #{$xxxlarge}\";\n      }\n\n      &::after,\n      &::before {\n        background: dodgerblue;\n      }\n    }\n  }\n}\n","/* ------------------------------------ *\\\n    $RESET\n\\* ------------------------------------ */\n\n/* Border-Box http:/paulirish.com/2012/box-sizing-border-box-ftw/ */\n*,\n*::before,\n*::after {\n  box-sizing: border-box;\n}\n\nbody {\n  margin: 0;\n  padding: 0;\n}\n\nblockquote,\nbody,\ndiv,\nfigure,\nfooter,\nform,\nh1,\nh2,\nh3,\nh4,\nh5,\nh6,\nheader,\nhtml,\niframe,\nlabel,\nlegend,\nli,\nnav,\nobject,\nol,\np,\nsection,\ntable,\nul {\n  margin: 0;\n  padding: 0;\n}\n\narticle,\nfigure,\nfooter,\nheader,\nhgroup,\nnav,\nsection {\n  display: block;\n}\n\naddress {\n  font-style: normal;\n}\n","/*!\n    Blueprint CSS 3.1.1\n    https://blueprintcss.dev\n    License MIT 2019\n*/\n\n@import './grid/_config';\n@import './grid/_base';\n@import './grid/_column-generator';\n@import './grid/_grid';\n@import './grid/_util';\n@import './grid/_spacing';\n","[#{$prefix}~='container'] {\n  width: 100%;\n  margin: 0 auto;\n  display: block;\n  max-width: $container-width;\n}\n","// grid modifiers\n[#{$prefix}~='grid'] {\n  display: grid !important;\n  grid-gap: $gutter;\n  grid-template-columns: repeat($cols, 1fr);\n}\n\n[#{$prefix}~='vertical-start'] {\n  align-items: start;\n}\n\n[#{$prefix}~='vertical-center'] {\n  align-items: center;\n}\n\n[#{$prefix}~='vertical-end'] {\n  align-items: end;\n}\n\n[#{$prefix}~='between'] {\n  justify-content: center;\n}\n\n[#{$prefix}~='gap-none'] {\n  grid-gap: 0;\n  margin-bottom: 0;\n}\n\n[#{$prefix}~='gap-column-none'] {\n  grid-column-gap: 0;\n}\n\n[#{$prefix}~='gap-row-none'] {\n  grid-row-gap: 0;\n  margin-bottom: 0;\n}\n\n// column modifiers\n[#{$prefix}~='first'] {\n  order: -1;\n}\n\n[#{$prefix}~='last'] {\n  order: $cols;\n}\n\n[#{$prefix}~='hide'] {\n  display: none !important;\n}\n\n[#{$prefix}~='show'] {\n  display: initial !important;\n}\n\n// implicit columns\n[#{$prefix}~='grid'][#{$prefix}*='\\@'] {\n  grid-template-columns: #{$cols}fr;\n}\n\n// explicit columns default\n[#{$prefix}~='grid'][#{$prefix}*='\\@sm'],\n[#{$prefix}~='grid'][#{$prefix}*='\\@md'],\n[#{$prefix}~='grid'][#{$prefix}*='\\@lg'],\n[#{$prefix}~='grid'][#{$prefix}*='\\@xl'] {\n  grid-template-columns: #{$cols}fr;\n}\n\n%full-width-columns-explicit {\n  grid-column: span $cols;\n}\n\n@for $i from 1 through $cols {\n  // explicit columns default\n  [#{$prefix}~='#{$i}\\@sm'],\n  [#{$prefix}~='#{$i}\\@md'],\n  [#{$prefix}~='#{$i}\\@lg'],\n  [#{$prefix}~='#{$i}\\@xl'] {\n    @extend %full-width-columns-explicit;\n  }\n}\n\n@for $i from 1 through $cols {\n  // implicit columns\n  [#{$prefix}~='grid'][#{$prefix}~='#{$i}'] {\n    grid-template-columns: repeat($cols / $i, 1fr);\n  }\n\n  // explicit columns\n  [#{$prefix}~='#{$i}'] {\n    grid-column: span $i / span $i;\n  }\n}\n\n@for $i from 1 through $cols {\n  [#{$prefix}~='offset-#{$i}'] {\n    grid-column-start: $i;\n  }\n}\n\n@media (min-width: $sm-break) {\n  @include column-generator('sm');\n}\n\n@media (min-width: $md-break) {\n  @include column-generator('md');\n}\n\n@media (min-width: $lg-break) {\n  @include column-generator('lg');\n}\n\n@media (min-width: $xl-break) {\n  @include column-generator('xl');\n}\n","@mixin column-generator($suffix) {\n  @for $i from 1 through $cols {\n    // implicit columns\n    [#{$prefix}~='grid'][#{$prefix}~='#{$i}\\@#{$suffix}'] {\n      grid-template-columns: repeat($cols / $i, 1fr);\n    }\n\n    // explicit columns\n    [#{$prefix}~='#{$i}\\@#{$suffix}'] {\n      grid-column: span $i / span $i;\n    }\n  }\n\n  @for $i from 1 through $cols {\n    [#{$prefix}~='offset-#{$i}\\@#{$suffix}'] {\n      grid-column-start: $i;\n    }\n  }\n\n  [#{$prefix}~='hide\\@#{$suffix}'] {\n    display: none !important;\n  }\n\n  [#{$prefix}~='show\\@#{$suffix}'] {\n    display: initial !important;\n  }\n\n  [#{$prefix}~='first\\@#{$suffix}'] {\n    orde