E%3C/svg%3E\");\n  background-repeat: no-repeat;\n  background-position: center center;\n  background-size: 10px;\n}\n\ninput[type=radio] {\n  border-radius: 50px;\n}\n\ninput[type=checkbox] {\n  border-radius: $border-radius;\n}\n\ninput[type=submit] {\n  transition: $transition-all;\n}\n\n/* clears the 'X' from Internet Explorer */\ninput[type=search]::-ms-clear {\n  display: none;\n  width: 0;\n  height: 0;\n}\n\ninput[type=search]::-ms-reveal {\n  display: none;\n  width: 0;\n  height: 0;\n}\n\n/* clears the 'X' from Chrome */\ninput[type=\"search\"]::-webkit-search-decoration,\ninput[type=\"search\"]::-webkit-search-cancel-button,\ninput[type=\"search\"]::-webkit-search-results-button,\ninput[type=\"search\"]::-webkit-search-results-decoration {\n  display: none;\n}\n\n/* removes the blue background on Chrome's autocomplete */\ninput:-webkit-autofill,\ninput:-webkit-autofill:hover,\ninput:-webkit-autofill:focus,\ninput:-webkit-autofill:active {\n  -webkit-box-shadow: 0 0 0 30px white inset;\n}\n\nselect {\n  background-color: $c-white;\n  appearance: none;\n  position: relative;\n  width: 100%;\n  padding-right: $space-and-half;\n}\n","/* ------------------------------------ *\\\n    $HEADINGS\n\\* ------------------------------------ */\n\n@mixin o-heading--xxl {\n  font-family: $ff-font--primary;\n  font-size: $font-size-xxl;\n  line-height: 1.3;\n}\n\nh1,\n.o-heading--xxl {\n  @include o-heading--xxl;\n}\n\n@mixin o-heading--xl {\n  font-family: $ff-font--primary;\n  font-size: $font-size-xl;\n  line-height: 1.3;\n}\n\nh2,\n.o-heading--xl {\n  @include o-heading--xl;\n}\n\n@mixin o-heading--l {\n  font-family: $ff-font--primary;\n  font-size: $font-size-l;\n  line-height: 1.4;\n}\n\nh3,\n.o-heading--l {\n  @include o-heading--l;\n}\n\n@mixin o-heading--m {\n  font-family: $ff-font--primary;\n  font-size: $font-size-m;\n  line-height: 1.4;\n}\n\nh4,\n.o-heading--m {\n  @include o-heading--m;\n}\n\n@mixin o-heading--s {\n  font-family: $ff-font--primary;\n  font-size: $font-size-s;\n  line-height: 1.6;\n}\n\nh5,\n.o-heading--s {\n  @include o-heading--s;\n}\n\n@mixin o-heading--xs {\n  font-family: $ff-font--primary;\n  font-size: $font-size-xs;\n  line-height: 1.5;\n}\n\nh6,\n.o-heading--xs {\n  @include o-heading--xs;\n}\n","/* ------------------------------------ *\\\n    $LINKS\n\\* ------------------------------------ */\n\na {\n  text-decoration: none;\n  color: $c-link-color;\n  transition: $transition-all;\n\n  &:hover {\n    color: $c-link-hover-color;\n  }\n}\n","/* ------------------------------------ *\\\n    $LISTS\n\\* ------------------------------------ */\n\nol,\nul {\n  margin: 0;\n  padding: 0;\n  list-style: none;\n}\n\n/**\n * Definition Lists\n */\ndl {\n  overflow: hidden;\n  margin: 0 0 $space;\n}\n\ndt {\n  font-weight: bold;\n}\n\ndd {\n  margin-left: 0;\n}\n","/* ------------------------------------ *\\\n    $SITE MAIN\n\\* ------------------------------------ */\n\nbody {\n  background: $c-white;\n  font: 400 16px / 1.3 $ff-font;\n  -webkit-text-size-adjust: 100%;\n  color: $c-body-color;\n  -webkit-font-smoothing: antialiased;\n  -moz-osx-font-smoothing: grayscale;\n  position: relative;\n}\n","/* ------------------------------------ *\\\n    $MEDIA ELEMENTS\n\\* ------------------------------------ */\n\n/**\n * Flexible Media\n */\nimg,\nvideo,\nobject,\nsvg,\niframe {\n  max-width: 100%;\n  border: none;\n  display: block;\n}\n\nimg {\n  height: auto;\n}\n\nsvg {\n  max-height: 100%;\n}\n\npicture,\npicture img {\n  display: block;\n}\n\nfigure {\n  position: relative;\n  display: inline-block;\n  overflow: hidden;\n}\n\nfigcaption {\n  a {\n    display: block;\n  }\n}\n\n/* ------------------------------------ *\\\n    $PRINT STYLES\n\\* ------------------------------------ */\n\n@media print {\n  *,\n  *::before,\n  *::after,\n  *::first-letter,\n  *::first-line {\n    background: transparent !important;\n    color: black !important;\n    box-shadow: none !important;\n    text-shadow: none !important;\n  }\n\n  a,\n  a:visited {\n    text-decoration: underline;\n  }\n\n  a[href]::after {\n    content: \" (\" attr(href) \")\";\n  }\n\n  abbr[title]::after {\n    content: \" (\" attr(title) \")\";\n  }\n\n  /*\n   * Don't show links that are fragment identifiers,\n   * or use the `javascript:` pseudo protocol\n   */\n  a[href^=\"#\"]::after,\n  a[href^=\"javascript:\"]::after {\n    content: \"\";\n  }\n\n  pre,\n  blockquote {\n    border: 1px solid #999;\n    page-break-inside: avoid;\n  }\n\n  /*\n   * Printing Tables:\n   * http://css-discuss.incutio.com/wiki/Printing_Tables\n   */\n  thead {\n    display: table-header-group;\n  }\n\n  tr,\n  img {\n    page-break-inside: avoid;\n  }\n\n  img {\n    max-width: 100% !important;\n    height: auto;\n  }\n\n  p,\n  h2,\n  h3 {\n    orphans: 3;\n    widows: 3;\n  }\n\n  h2,\n  h3 {\n    page-break-after: avoid;\n  }\n\n  .no-print,\n  .c-header,\n  .c-footer,\n  .ad {\n    display: none;\n  }\n}\n","/* ------------------------------------ *\\\n    $TABLES\n\\* ------------------------------------ */\n\ntable {\n  border-collapse: collapse;\n  border-spacing: 0;\n  border: 1px solid $c-gray;\n  width: 100%;\n}\n\nth {\n  text-align: left;\n  border: 1px solid transparent;\n  padding: ($space / 2) 0;\n  text-transform: uppercase;\n  vertical-align: top;\n  font-weight: bold;\n}\n\ntr {\n  border: 1px solid transparent;\n}\n\ntd {\n  border: 1px solid transparent;\n  padding: $space / 2;\n}\n\n/**\n * Responsive Table\n */\n.c-table--responsive {\n  border: 1px solid $c-gray;\n  border-collapse: collapse;\n  padding: 0;\n  width: 100%;\n\n  tr {\n    border: 1px solid $c-gray;\n    background-color: $c-gray--light;\n  }\n\n  th,\n  td {\n    padding: $space / 2;\n  }\n\n  th {\n    font-size: $font-size-xs;\n    text-transform: uppercase;\n    border-bottom: 1px solid $c-gray;\n  }\n\n  @include media(\"<=medium\") {\n    border: 0;\n\n    thead {\n      border: none;\n      clip: rect(0 0 0 0);\n      height: 1px;\n      margin: -1px;\n      overflow: hidden;\n      padding: 0;\n      position: absolute;\n      width: 1px;\n    }\n\n    tr {\n      border-bottom: 3px solid $c-gray;\n      display: block;\n      margin-bottom: $space / 2;\n\n      &.this-is-active {\n        td:not(:first-child) {\n          display: flex;\n        }\n\n        td:first-child::before {\n          content: \"- \" attr(data-label);\n        }\n      }\n    }\n\n    td {\n      border-bottom: 1px solid $c-gray;\n      display: flex;\n      align-items: center;\n      justify-content: space-between;\n      min-height: 40px;\n\n      &:first-child {\n        cursor: pointer;\n\n        &::before {\n          content: \"+ \" attr(data-label);\n        }\n      }\n\n      &:last-child {\n        border-bottom: 0;\n      }\n\n      &:not(:first-child) {\n        display: none;\n      }\n\n      &::before {\n        content: attr(data-label);\n        font-weight: bold;\n        text-transform: uppercase;\n        font-size: $font-size-xs;\n      }\n    }\n  }\n}\n","@charset \"UTF-8\";\n\n//     _            _           _                           _ _\n//    (_)          | |         | |                         | (_)\n//     _ _ __   ___| |_   _  __| | ___   _ __ ___   ___  __| |_  __ _\n//    | | '_ \\ / __| | | | |/ _` |/ _ \\ | '_ ` _ \\ / _ \\/ _` | |/ _` |\n//    | | | | | (__| | |_| | (_| |  __/ | | | | | |  __/ (_| | | (_| |\n//    |_|_| |_|\\___|_|\\__,_|\\__,_|\\___| |_| |_| |_|\\___|\\__,_|_|\\__,_|\n//\n//      Simple, elegant and maintainable media queries in Sass\n//                        v1.4.9\n//\n//                http://include-media.com\n//\n//         Authors: Eduardo Boucas (@eduardoboucas)\n//                  Hugo Giraudel (@hugogiraudel)\n//\n//      This project is licensed under the terms of the MIT license\n\n////\n/// include-media library public configuration\n/// @author Eduardo Boucas\n/// @access public\n////\n\n///\n/// Creates a list of global breakpoints\n///\n/// @example scss - Creates a single breakpoint with the label `phone`\n///  $breakpoints: ('phone': 320px);\n///\n$breakpoints: (\n  'phone': 320px,\n  'tablet': 768px,\n  'desktop': 1024px\n) !default;\n\n///\n/// Creates a list of static expressions or media types\n///\n/// @example scss - Creates a single media type (screen)\n///  $media-expressions: ('screen': 'screen');\n///\n/// @example scss - Creates a static expression with logical disjunction (OR operator)\n///  $media-expressions: (\n///    'retina2x': '(-webkit-min-device-pixel-ratio: 2), (min-resolution: 192dpi)'\n///  );\n///\n$media-expressions: (\n  'screen': 'screen',\n  'print': 'print',\n  'handheld': 'handheld',\n  'landscape': '(orientation: landscape)',\n  'portrait': '(orientation: portrait)',\n  'retina2x': '(-webkit-min-device-pixel-ratio: 2), (min-resolution: 192dpi), (min-resolution: 2dppx)',\n  'retina3x': '(-webkit-min-device-pixel-ratio: 3), (min-resolution: 350dpi), (min-resolution: 3dppx)'\n) !default;\n\n///\n/// Defines a number to be added or subtracted from each unit when declaring breakpoints with exclusive intervals\n///\n/// @example scss - Interval for pixels is defined as `1` by default\n///  @include media('>128px') {}\n///\n///  /* Generates: */\n///  @media(min-width: 129px) {}\n///\n/// @example scss - Interval for ems is defined as `0.01` by default\n///  @include media('>20em') {}\n///\n///  /* Generates: */\n///  @media(min-width: 20.01em) {}\n///\n/// @example scss - Interval for rems is defined as `0.1` by default, to be used with `font-size: 62.5%;`\n///  @include media('>2.0rem') {}\n///\n///  /* Generates: */\n///  @media(min-width: 2.1rem) {}\n///\n$unit-intervals: (\n  'px': 1,\n  'em': 0.01,\n  'rem': 0.1,\n  '': 0\n) !default;\n\n///\n/// Defines whether support for media queries is available, useful for creating separate stylesheets\n/// for browsers that don't support media queries.\n///\n/// @example scss - Disables support for media queries\n///  $i